<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="papers" style="width: 100%">
          <el-table-column label="问卷ID" prop="paperID" v-if="show = false"></el-table-column>
          <el-table-column label="问卷名称" prop="paperName"></el-table-column>
          <el-table-column label="创建时间" prop="createTime"></el-table-column>
          <el-table-column align="right">
            <template slot="header">
              <el-button size="small" @click="add" type="primary">
                <i class="el-icon-circle-plus-outline" />创建问卷
              </el-button>
            </template>
            <template slot-scope="scope">
              <el-button size="small" type="success" @click="select(scope.row)" round>
                <i class="el-icon-search" />查看
              </el-button>
              <el-button size="small" type="danger" @click="deletePaper(scope.row)" round>
                <i class="el-icon-delete-solid" />删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  inject: ["reload"],
  data() {
    return {
      currentPage: 1,
      total: 0,
      papers: []
    };
  },
  methods: {
    add() {
      this.$router.push({ name: "newPaper" });
    },
    select(row) {
      this.$router.push({
        name: "paperDetail",
        params: { paperID: row.paperID }
      });
    },
    deletePaper(row) {
      console.log("delete对象：" + JSON.stringify(row));
      var api = "http://localhost:8888/paper/" + row.paperID;
      this.axios
        .delete(api)
        .then(response => {
          console.log(response);
          this.$message({
            showClose: true,
            message: "操作成功",
            type: "success"
          });
          this.reload();
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      var api = "http://localhost:8888/paper/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.papers = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var api =
        "http://localhost:8888/paper/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.papers = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
</style>